/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #eeeeee;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Noto Sans", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
}

h1, h2, h3, h4 {
  font-family: "Noto Sans", sans-serif;
  margin: 1rem auto;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

p {
  font-size: 2rem;
  margin: 0 auto;
  font-family: "Raleway", sans-serif;
}

span {
  color: #329f00;
}

header {
  background: rgb(0, 0, 0);
  padding: 1.6rem 0;
  background: linear-gradient(90deg, rgb(0, 12, 18) 0%, rgba(0, 12, 18, 0.6855336736) 50%, rgb(0, 12, 18) 100%);
  position: sticky;
  backdrop-filter: blur(5px);
  left: 0;
  top: 0;
  right: 0;
  z-index: 100;
}

.logo {
  display: flex;
  flex-direction: row;
  align-items: center;
  /* margin: .5rem 0; */
}
@media (min-width: 480px) {
  .logo {
    margin: 0;
  }
}
.logo img {
  width: 5rem;
}
.logo .logo__txt {
  color: #eeeeee;
  width: 4rem;
}
@media (min-width: 480px) {
  .logo .logo__txt {
    width: auto;
  }
}
.logo .logo__txt span {
  font-family: "Noto Sans", sans-serif;
  color: #329f00;
}

.barra {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 0;
  align-items: flex-start;
}

.log-header i {
  font-size: 5rem;
  color: #329f00;
}

.menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: all 0.3s;
}
.menu i {
  font-size: 5rem;
  color: #eeeeee;
  cursor: pointer;
}
.menu.menu-active {
  height: 100vh;
  transition: all 0.3s;
}

.menu-open {
  visibility: visible;
  height: auto;
  width: auto;
  opacity: 1;
}
.menu-open.menu-open_active {
  visibility: hidden;
  height: 0;
  width: 0;
  opacity: 0;
}

.menu-close {
  visibility: hidden;
  height: 0;
  width: 0;
  opacity: 0;
}
.menu-close.menu-close_active {
  visibility: visible;
  height: auto;
  width: auto;
  opacity: 1;
}

.nav {
  visibility: hidden;
  height: 0;
  width: 0;
  opacity: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
  transition: all 0.5s;
}
.nav a, .nav p {
  color: #eeeeee;
  font-size: 2rem;
  text-align: center;
  display: none;
}
.nav.nav_active {
  transform: translateY(100%);
  visibility: visible;
  width: auto;
  height: auto;
  opacity: 1;
  transition: all 0.5s;
}
.nav.nav_active a, .nav.nav_active p {
  display: block;
  transition: all 0.3s linear;
}
.nav.nav_active a:hover, .nav.nav_active p:hover {
  color: #329f00;
}

.hero {
  margin: 4rem auto;
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
}
@media (min-width: 1024px) {
  .hero {
    display: grid;
    grid-template-columns: 33% 66%;
    align-self: center;
    width: 100%;
  }
}
.hero .derecha {
  max-width: 60rem;
  margin: 0 auto;
}
.hero .derecha img {
  box-shadow: -2px 2px 20px -6px rgba(0, 0, 0, 0.75);
  width: 100%;
  opacity: 0.8;
}
.hero .izquierda {
  margin-top: 2rem;
}
.hero .izquierda .logo-hero {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 1024px) {
  .hero .izquierda .logo-hero {
    justify-content: flex-end;
  }
}
.hero .izquierda .logo-hero img {
  filter: invert(100%);
  width: 100%;
  max-width: 8rem;
}
.hero .izquierda h1, .hero .izquierda h2, .hero .izquierda p {
  margin: 0 auto;
  text-align: center;
}
@media (min-width: 1024px) {
  .hero .izquierda h1, .hero .izquierda h2, .hero .izquierda p {
    text-align: right;
  }
}
.hero .izquierda h2 {
  margin-bottom: 1rem;
}

footer {
  background: rgb(0, 0, 0);
  background: linear-gradient(90deg, rgb(0, 12, 18) 0%, rgba(0, 12, 18, 0.6855336736) 50%, rgb(0, 12, 18) 100%);
  position: sticky;
  padding-top: 4rem;
  margin-top: auto;
}

.info {
  display: flex;
  align-items: center;
  background-color: #32A78A;
  box-shadow: -2px 2px 20px -6px rgba(0, 0, 0, 0.75);
  padding: 4rem 2rem;
  margin: 0 auto 3rem;
  border-radius: 10px;
}
@media (min-width: 768px) {
  .info {
    display: grid;
    grid-template-columns: 33% 66%;
  }
}
.info .info__texto {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  color: #eeeeee;
  margin: 0 auto;
  gap: 1.6rem;
  width: 100%;
}
@media (min-width: 768px) {
  .info .info__texto {
    align-items: flex-start;
    width: auto;
  }
}
.info .info__texto h3 {
  margin: 0 auto;
  text-align: center;
  width: 100%;
}
@media (min-width: 768px) {
  .info .info__texto h3 {
    margin: 0;
    text-align: left;
  }
}
.info .info__img {
  margin: 0 auto;
  display: none;
}
.info .info__img img {
  width: 20rem;
}
@media (min-width: 768px) {
  .info .info__img {
    display: block;
  }
}

.form__suscribir {
  display: flex;
  flex-direction: row;
  gap: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .form__suscribir {
    width: auto;
  }
}
.form__suscribir input {
  padding: 2rem 1.6rem;
  border: none;
  background-color: #dadada;
  color: #000C12;
  border-radius: 10px;
  width: 100%;
  transition: background-color 0.3s linear;
}
@media (min-width: 480px) {
  .form__suscribir input {
    width: auto;
  }
}
.form__suscribir input:hover {
  background-color: #cdcdcd;
}
.form__suscribir .boton__suscribir {
  padding: 1.4rem 1rem;
  border: none;
  background-color: #000C12;
  color: #dadada;
  border-radius: 10px;
  margin-left: -1rem;
  cursor: pointer;
  transition: background-color 0.3s linear, color 0.3s linear;
}
.form__suscribir .boton__suscribir:hover {
  background-color: #329f00;
  color: #eeeeee;
}

.extra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.extra h4 {
  text-align: center;
  color: #eeeeee;
  font-size: 2.4rem;
}
.extra .extra__redes {
  display: flex;
  gap: 2rem;
  justify-content: space-around;
}
.extra .extra__redes a {
  color: #eeeeee;
  transition: color 0.3s;
}
.extra .extra__redes a:hover {
  color: #329f00;
}
.extra .extra__redes svg {
  transition: filter 0.3s linear;
}
.extra .extra__redes svg:hover {
  filter: brightness(80%);
}

.copyright {
  display: block;
  text-align: center;
  color: #eeeeee;
  font-size: 1.4rem;
  margin: 3rem auto 0;
  padding-bottom: 4rem;
}

.formulario {
  margin: 0 auto;
  padding: 0 1rem;
  width: 100%;
}
.formulario .campo {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  margin: 2rem 0;
}
.formulario .campo label {
  font-family: "Raleway", sans-serif;
  color: #000C12;
}
.formulario .campo textarea {
  height: 15rem;
}

.campo input, .campo textarea {
  outline: none;
  padding: 0.6rem 1.2rem;
  border-radius: 8px;
  border: 1px #dadada solid;
  color: #000C12;
  width: 100%;
  transition: border-color 0.3s linear;
  background-color: transparent;
}
.campo input:hover, .campo textarea:hover {
  border-color: #329f00;
}
.campo input:focus, .campo textarea:focus {
  border-color: #32A78A;
}

.bg-resumen {
  width: 100%;
  background-color: #32A78A;
  height: 100%;
  margin: 5rem auto;
}

.resumen {
  display: flex;
  flex-direction: column-reverse;
  gap: 2rem;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 90%;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .resumen {
    display: grid;
    grid-template-columns: 60% 40%;
  }
}
.resumen .izquierda h2,
.resumen .izquierda p {
  color: #eeeeee;
  text-align: center;
}
.resumen .izquierda p::before {
  content: "✔️ ";
}
.resumen .derecha img {
  width: 100%;
  box-shadow: -2px 2px 20px -6px rgba(0, 0, 0, 0.75);
}

.courses {
  padding-bottom: 4rem;
  border-bottom: 1px #dadada solid;
}
.courses h2 {
  margin: 5rem auto;
}

.cards-courses {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;
  gap: 3rem;
  margin: 0 auto;
}
@media (min-width: 480px) {
  .cards-courses {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .cards-courses {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: nowrap;
  border: none;
  border-radius: 10px;
  box-shadow: -2px 2px 20px -6px rgba(0, 0, 0, 0.75);
  padding: 0;
  width: 100%;
}
.card .card__img img {
  width: 100%;
  padding: 0;
  object-fit: cover;
}

.card__contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 1rem 2rem;
}
.card__contenido h3 {
  margin: 0 auto 1rem;
  font-size: 2.6rem;
}
.card__contenido p {
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
  text-align: center;
  text-transform: 300;
}
@media (min-width: 480px) {
  .card__contenido p {
    text-align: left;
  }
}
.card__contenido .valor {
  margin: 1rem auto;
}
.card__contenido .valor .precio, .card__contenido .valor .descuento {
  font-size: 2.6rem;
  text-transform: uppercase;
  font-weight: bold;
}
.card__contenido .valor .descuento {
  font-size: 2rem;
}
.card__contenido .valor .precio-descuento {
  color: #BF0502;
  text-decoration: line-through;
}

.feedback {
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin: 5rem auto;
}
@media (min-width: 768px) {
  .feedback {
    display: grid;
    grid-template-columns: 66% 33%;
    align-items: unset;
  }
}

.perfiles {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.perfil {
  background-color: #32A78A;
  padding: 3rem;
  border-radius: 10px;
  margin: 0 auto;
}

.perfil {
  display: grid;
  grid-template-columns: 15% 85%;
  gap: 3rem;
}
.perfil .perfil__img img {
  width: 100%;
}
.perfil .perfil__detalles {
  display: flex;
  flex-direction: column;
  gap: 0;
  justify-content: center;
}
.perfil .perfil__detalles p {
  color: #eeeeee;
}
.perfil .perfil__detalles .perfil-fecha {
  color: #dadada;
  font-size: 1.6rem;
  margin: 0.5rem 0;
}
.perfil .perfil__detalles .perfil__comentario {
  padding: 0 3rem 0 0;
}

.post__feedback {
  display: none;
}
@media (min-width: 768px) {
  .post__feedback {
    display: block;
  }
}

.post__feedback-responsive {
  display: block;
}
@media (min-width: 768px) {
  .post__feedback-responsive {
    display: none;
  }
}

.form-feedback {
  margin: 0 auto;
  display: grid;
}
.form-feedback textarea {
  margin: 2rem auto;
  padding: 1rem 2rem;
  color: #000C12;
  border: 1px #dadada solid;
  border-radius: 10px;
  width: 90%;
}
@media (min-width: 480px) {
  .form-feedback textarea {
    width: 75%;
  }
}
@media (min-width: 768px) {
  .form-feedback textarea {
    width: 90%;
    height: 15rem;
  }
}

.hidden {
  opacity: 0;
  height: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.4s ease-in, height 0.5s linear, visibility 0.3s linear;
}

.displayNone {
  display: none;
}

.boton {
  border: 1px solid #329f00;
  background-color: transparent;
  color: #329f00;
  border-radius: 15px;
  display: block;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem 2rem;
  text-align: center;
  font-weight: 700;
  transition: border-color 0.3s linear, color 0.3s linear, background-color 0.3s linear;
}
.boton:hover {
  border-color: #329f00;
  background-color: #329f00;
  color: #eeeeee;
  cursor: pointer;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}

.btn__viewmore {
  border: 1px solid #329f00;
  background-color: transparent;
  color: #329f00;
  border-radius: 15px;
  display: table;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem 2rem;
  text-align: center;
  font-weight: 700;
  transition: border-color 0.3s linear, color 0.3s linear, background-color 0.3s linear;
  transition: background-color 0.3s linear;
  font-size: 2rem;
  padding: 2rem 3rem;
  width: auto;
  margin: 4rem auto;
}
.btn__viewmore:hover {
  border-color: #329f00;
  background-color: #329f00;
  color: #eeeeee;
  cursor: pointer;
}
@media (min-width: 768px) {
  .btn__viewmore {
    width: auto;
  }
}
.btn__viewmore:hover {
  background-color: #329f00;
}

.btn-feedback {
  border: 1px solid #32A78A;
  background-color: transparent;
  color: #32A78A;
  border-radius: 15px;
  display: block;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem 2rem;
  text-align: center;
  font-weight: 700;
  transition: border-color 0.3s linear, color 0.3s linear, background-color 0.3s linear;
  width: 90%;
}
.btn-feedback:hover {
  border-color: #32A78A;
  background-color: #32A78A;
  color: #eeeeee;
  cursor: pointer;
}
@media (min-width: 768px) {
  .btn-feedback {
    width: auto;
  }
}
@media (min-width: 480px) {
  .btn-feedback {
    width: auto;
  }
}

.btn-curso {
  border: 1px solid #eeeeee;
  background-color: transparent;
  color: #eeeeee;
  border-radius: 15px;
  display: block;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem 2rem;
